<template>
  <zTitle level="1" text="">
    <zExample title="">
      <el-form :model="formData" label-width="120px" style="margin-top: 60px;">
        <commonFormItem :map="formMap" :formData="formData" />
      </el-form>
      <template v-slot:document>
        <exp1 />
      </template>
    </zExample>
    <zExample title="">
      <el-form :model="formData" label-width="120px" style="margin-top: 60px;">
        <commonFormItem :map="formMap1" :formData="formData" />
      </el-form>
      <template v-slot:document>
        <exp2 />
      </template>
    </zExample>
    <testMD />
  </zTitle>
</template>
