  import commonFormItem from "@/components/Common/formItem"

  import testMD from "@/assets/markdown/components/formItem/index.md";
  import exp1 from "@/assets/markdown/components/formItem/exp1.md";
  import exp2 from "@/assets/markdown/components/formItem/exp2.md";
  export default {
    components: {
      commonFormItem,
      testMD,
      exp1,
      exp2
    },
    data() {
      return {
        formData: {
          fp1: '1'
        },
        formMap: [
          [{
            label: '',
            model: 'memberCompanyName',
            required: true,
          }, {
            label: '',
            model: 'sampleName',
            required: true,
          }, {
            label: '',
            model: 'sampleName',
            required: true,
          }],
          [{
            label: '',
            model: 'sampleSpecificationName',
            required: true,
            template: {
              optionList: []
            }
          }, {
            label: '',
            model: 'sampleModelName',
            required: true,
            template: {
              optionList: []
            }
          }, {
            label: '1',
            model: 'otherSampleModelName',
            required: true,
            template: {
              optionList: []
            }
          }, {
            label: '2',
            model: 'otherSampleModelName',
            required: true,
            template: {
              optionList: []
            }
          }],
          [{
            label: '',
            model: 'representBatch',
            span: 6,
            brother: [{
              compName: 'relationInput',
              model: 'representBatchUnit',
              list: []
            }],
          }, {
            label: '',
            model: 'sampleCount',
            span: 6,
            required: true,
            brother: [{
              compName: 'relationInput',
              model: 'representBatchUnit',
              list: []
            }],
          }, {
            label: '',
            span: 3,
            model: 'additionalSampleModel',
          }, {
            label: '',
            model: 'additionalSampleModel',
          }]
        ],
        formMap1: [
          [{
            label: '',
            model: 'fp1',
            required: true,
            type: 'radio',
            template: {
              radioList: [{
                label: '1',
                value: '1'
              }, {
                label: '2',
                value: '2'
              }, {
                label: '3',
                value: '3'
              }, {
                label: '4',
                value: '4'
              }]
            }
          }, {
            type: 'text',
            required: true,
            show(data) {
              return data.fp1 == 1;
            },
            template: {
              text: '1',
              style: 'color: red'
            }
          }],
          [{
            label: '',
            type: 'date',
            model: 'fp2'
          }, {
            label: '',
            type: 'select',
            model: 'fp3',
            template: {
              optionList: [{
                label: '1',
                value: 'key1'
              }, {
                label: '2',
                value: 'key2'
              }]
            }
          }],
          [{
            model: 'fp4',
            label: '',
            disabled: true
          }, {
            label: '',
            brother: [{
              compName: 'relationInput', // cascaderInput , relationInput 
              model: 'representBatchUnit',
              list: []
            }],
          }],
          [{
            type: 'text',
            model: 'fp5',
            template: {
              text: 'xxxxxx',
              style: 'color:#333;'
            }
          }],
          [{
            label: '',
            model: 'fp6',
            type: 'textarea',
          }]
        ]
      }
    },
    methods: {
      submit() {
        this.$refs.form.validate(vaild => {
          vaild ? this.$message.success('') : '';
        })
      }
    }
  }
</script>
